/**
 * 工会信息相关store
 */

import request from "@/utils/request";

const association = {
  namespaced: true,
  state: {
    // 工会信息
    associationInfo: {},
  },
  mutations: {
    // 设置工会信息
    setAssociationInfo(state, associationInfo) {
      state.associationInfo = associationInfo;
    },
  },
  actions: {
    /**
     * 通过域名获取工会信息
     */
    async associationInfoDomainAPI({ commit }) {
      const domain = window.location.hostname;
      console.log("domain :>> ", domain);
      const data = {
        // 古田
        domain:
          process.env.NODE_ENV === "development"
            ? "gt.screen.fsa.tuuz.ltd"
            : domain,

        // 福建省
        // domain:
        //   process.env.NODE_ENV === "development"
        //     ? "fj.screen.fsa.tuuz.ltd"
        //     : domain,
      };
      const res = await request("association.associationInfoDomain", data);
      console.log("通过域名获取API_res :>> ", res);
      commit("setAssociationInfo", res);
    },
  },
};

export default association;
