/**
 * 显示数据过滤相关store
 */

const viewFilter = {
  namespaced: true,
  state: {
    // 近期宣讲区域多选列表
    preachingAreaCheckedList: [
      {
        label: "省",
        value: "province",
        checked: true,
      },
      {
        label: "市",
        value: "city",
        checked: true,
      },
      {
        label: "区",
        value: "district",
        checked: true,
      },
      {
        label: "街道",
        value: "street",
        checked: true,
      },
    ],
    // 宣讲区域覆盖人数排名多选列表
    coverListCheckedList: [
      {
        label: "市",
        value: "city",
        checked: true,
      },
      {
        label: "区",
        value: "district",
        checked: true,
      },
      {
        label: "街道",
        value: "street",
        checked: true,
      },
    ],
  },
  mutations: {
    // 设置近期宣讲区域多选列表
    setPreachingAreaCheckedList(state, preachingAreaCheckedList) {
      state.preachingAreaCheckedList = preachingAreaCheckedList;
    },
    // 设置近期宣讲区域多选列表
    setCoverListCheckedList(state, coverListCheckedList) {
      state.coverListCheckedList = coverListCheckedList;
    },
  },
  actions: {},
};

export default viewFilter;
